@font-face{font-family:'Nunito';src:url("../fonts/NunitoSans-Regular.ttf")}*{box-sizing:border-box;padding:0;margin:0;color:#f0f2ec;font-family:Nunito,sans-serif}body,html,#root{height:100%}body{background-color:#253c40}.page{height:100%;width:100%;display:grid;grid-template-rows:auto 1fr auto}.header{position:relative;padding:25px;width:100%;background-color:#111;display:flex;flex-flow:row nowrap;justify-content:center;align-items:center}.header__title{font-size:35px;color:#f0f2ec}.header__link{position:absolute;right:20px;padding:5px;font-size:21px;text-decoration:none;color:#f0f2ec}.header__link:hover{text-decoration:underline}.header__link:link,.header__link:visited{color:#f0f2ec}.footer{background-color:#212121;padding:5px}.cv__display{width:80%;margin:50px auto;height:fit-content;max-width:650px;background-color:#5a4e56;box-shadow:-1px 1px 2px 2px #748ea4;padding:10px 20px;border-radius:5px}.cv__builder{width:100%;height:100%}.display-section{padding:10px}.display-section__header{font-size:24.5px;color:#f0f2ec;font-family:'Arial', 'Calibri', 'Helvetica'}.display-section__info{margin:5px auto;font-family:'Arial', 'Calibri', 'Helvetica';background-color:#756670;box-shadow:-1px 1px 1px 1px #7b7c6b;border-radius:5px;padding:10px}.display-section__detail{font-family:'Arial', 'Calibri', 'Helvetica';padding:2px}.display-section__detail--emphasis{font-size:16.8px;font-weight:bold}.display-section__detail--small{font-size:12.6px}.form{position:absolute;top:15%;left:0;right:0;margin-left:auto;margin-right:auto;width:fit-content;max-width:1000px;max-width:90%;display:grid;grid-template-rows:auto}.form--shown{transition:opacity 1000ms ease-in 500ms, height 1000ms ease-in 500ms;height:auto;opacity:1}.form--hidden{transition:opacity 500ms ease-in 0ms, height 500ms ease-in 0ms;animation:hideForm 1s ease 0s 1 forwards;opacity:0;height:0}.form__header{font-size:35px;text-align:center}.form__inputs{background-color:#756670;box-shadow:-1px 1px 1px 1px #4d5158;border-radius:5px;padding:30px 10px}.form__field{display:grid;grid-template-columns:1fr 4fr;margin-top:5px}.form__label{font-size:17.5px;padding-right:5px;text-align:right}.form__input{padding:5px 10px;width:auto;color:#212121}.form__btns{display:flex;flex-flow:row nowrap;justify-content:center}.form__continue{background-color:#58a569;border:1px solid #4f945e;outline:none;cursor:pointer}.form__continue:hover{-webkit-box-shadow:0 0 5px 1px #67866d;box-shadow:0 0 5px 1px #67866d}.form__continue:active{box-shadow:inset 0 0 1px 1px #468454}.form__more{background-color:#748ea4;border:1px solid #65819a;outline:none;cursor:pointer}.form__more:hover{-webkit-box-shadow:0 0 5px 1px #757a8a;box-shadow:0 0 5px 1px #757a8a}.form__more:active{box-shadow:inset 0 0 1px 1px #5b748a}.form__back{background-color:#f44336;border:1px solid #f32c1e;outline:none;cursor:pointer}.form__back:hover{-webkit-box-shadow:0 0 5px 1px #b55553;box-shadow:0 0 5px 1px #b55553}.form__back:active{box-shadow:inset 0 0 1px 1px #ea1c0d}.form__back,.form__more,.form__continue{color:#212121;margin:5px;padding:10px;font-size:17.5px}@keyframes hideForm{99%{visibility:initial}100%{visibility:hidden}}
